import React, { Component } from 'react';
import { connect } from 'react-redux';
import AreaList from '../../components/AreaList'
import './styles.css';
import { findArea } from '../../api/area'

class HomePage extends Component {
    constructor(props){
        super(props)
        this.state = {
            areas: [],
            locations: null
        }
        this.getLocation()
    }
    handleAreas(areas){
        this.setState({areas: areas})
    }
    consumeLocation(o){
        findArea({
            lat: o.coords.latitude,
            long: o.coords.longitude
        }).then((res)=>{
            this.handleAreas(res.data)
        })
        this.setState({loc: o})
    }
    getLocation() {
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(this.consumeLocation.bind(this))
        } else {
            console.log("Geolocation is not supported by this browser.")
        }
    }
    render() {
        var location = ""
        return (
            <div className="wrap">
                <div>Your areas</div>
                <AreaList areas={this.props.areas} />
                <div>Open areas</div>
                <OpenAreaList areas={this.state.areas} />
            </div>
        );
    }
}

function mapStateToProps(state) {
    return { areas: state.areas }
}

export default connect(mapStateToProps)(HomePage)



